@font-face {
  font-family: "PTsans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-weight: 400;
}
@font-face {
  font-family: "PTsans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.page__body {
  font-family: PTsans, Arial, sans-serif;
  font-weight: normal;
  font-style: normal;
  width: 320px;
  padding: 0 20px;
  margin: 0 auto;
  text-transform: uppercase;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}
.page-header {
  background-color: #ffffff;
  text-align: center;
}
.page-header__logo {
  text-align: center;
}
.intro {
  text-align: center;
  height: 389px;
}
.main-nav {
  background-color: #81B3D2;
  text-align: center;
  height: 223px;
}
.main-nav__toggle {
  width: 22px;
  height: 22px;
  background-color: #81B3D2;
}
.main-nav__list {
  list-style: none;
}
.main-nav__link {
  font-size: 18px;
  font-weight: 700;
  line-height: 40px;
  text-transform: uppercase;
  text-decoration: none;
  color: #FFFFFF;
}
.advantages {
  text-align: center;
  background-color: #FFFFFF;
}
.advantages-header__title {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
}
.advantages-header__text {
  font-size: 14px;
  line-height: 26px;
  font-weight: 400;
}
.advantages-big-block {
  background-color: #81B3D2;
  color: #FFFFFF;
  text-align: center;
}
.advantage__title {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
}
.advantage__number {
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
}
.advantage__text {
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
}
.advantages-small-block {
  text-align: center;
}
.advantages-small-block--grey {
  background-color: #EEEEEE;
}
.booking {
  text-align: center;
}
.booking__title {
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
}
.booking__text {
  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
}
.booking__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 280px;
  height: 58px;
  background-color: #81B3D2;
  color: #FFFFFF;
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
}
.booking__button:hover {
  background-color: #669EC0;
}
.booking__button:active {
  background-color: #5496BD;
  color: rgba(255, 255, 255, 0.7);
}
.main-footer {
  text-align: center;
  height: 233px;
}
.main-footer__slogan {
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
}
.social__link {
  background-color: #81B3D2;
}
.social__link:hover {
  background-color: #669EC0;
}
.social__link:active {
  background-color: #5496BD;
}
.develop__title {
  font-size: 14px;
  line-height: 26px;
  font-weight: 400;
}
.reviews__title {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
}
.reviews__text {
  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
}
.reviews-form__title {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
}
.reviews-form__label {
  font-size: 14px;
  line-height: 26px;
  font-weight: 400;
}
.reviews-form__input {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  border-style: none;
  background-color: #F2F2F2;
}
.reviews-form__input::placeholder {
  color: #000000;
}
.reviews-form__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 280px;
  height: 58px;
  background-color: #81B3D2;
  color: #FFFFFF;
  border-style: none;
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
  text-transform: uppercase;
}
.reviews-form__button:hover {
  background-color: #669EC0;
  outline: none;
  border: none;
}
.reviews-form__button:active {
  background-color: #5496BD;
  color: rgba(255, 255, 255, 0.7);
  outline: none;
  border: none;
}
.impression {
  border-style: none;
}
.impression__title {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
}
.impression__label {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-transform: none;
}
.visited {
  border-style: none;
}
.visited__title {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
}
.visited__label {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-transform: none;
}
.form-letter__label {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
}
.form-letter__textarea {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  background-color: #F2F2F2;
}
.form-letter__textarea::placeholder {
  color: #000000;
}
